import React from "react";
import styles from './orderDetails.module.css'
import PropTypes from "prop-types";
import { useSelector } from "react-redux";
import Preloader from "../Preloader/Preloader";


/*     ,       */
const OrderDetails = () => {
  const currentOrderNumber = useSelector(store => store.orderDetails.currentOrderNumber);
  const isLoading = useSelector(store => store.orderDetails.orederRequest); 
  const isRequestSuccess = useSelector(store => store.orderDetails.orderSuccess);
  const requestFailed = useSelector(store => store.orderDetails.orderfailed)
  
  {isLoading && (
      <Preloader />
  )}
    if (isRequestSuccess) {
      return (
        <div className={`${styles.orderDetails} pt-30 pb-30`}>
        <h3 className='styles.orderDetails__title text text_type_digits-large'>{ currentOrderNumber }</h3>
        <p className='text text_type_main-medium pt-8 pb-15'> </p>
        <img className={styles.orderDetails__image} src={require('./images/order accpeted-popup-graphics.png')} alt='  ' />
        <p className='text text_type_main-default pt-15 pb-2'>   </p>
        <p className='text text_type_main-default text_color_inactive'>    </p>
      </div>
      )
    } else if (requestFailed) {
      return (
        <div className={`${styles.orderDetails} pt-30 pb-30`}>
          <h3 className='styles.orderDetails__title text text_type_digits-medium'> ,  </h3>
        </div>
      )
    }
};

 /*   ,    */
/* OrderDetails.propTypes = {
  currentOrderNumber: PropTypes.object.isRequired
};  */

export default OrderDetails;